<template>
<div class="col-sm-6 col-xl-4">
  <div class="card mb-4">
    <h6 class="card-header with-elements">
      <div class="card-header-title">Metas</div>
    </h6>
    <div class="card-body height">
      <apexchart type=radialBar height=200 :options="chartOptions" :series="[$store.state.metaPercent]"/>
      <div class="metas">
        <div class="meta">
          <div class="left">Meta: </div>
          <div class="right">{{numberWithCommas($store.state.meta)}}</div>
        </div>
        <div class="meta">
          <div class="left">Faltan: </div>
          <div class="right">{{numberWithCommas($store.state.metaResto)}}</div>
        </div>
      </div>
    </div>
      
  </div>
</div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'
import helperComma from '../../helpers/comma'
export default {
  name: 'ChartMetas',
  components: {
    apexchart: VueApexCharts,
  },
  data() {
    return {
      series: [45],
      chartOptions: {
        chart: {
          toolbar: {
            show: true
          }
        },
        plotOptions: {
          radialBar: {
            startAngle: -135,
            endAngle: 225,
            hollow: {
              margin: 0,
              size: '70%',
              background: '#fff',
              position: 'front',
              dropShadow: {
                enabled: true,
                top: 3,
                left: 0,
                blur: 4,
                opacity: 0.24
              }
            },
            track: {
              background: '#fff',
              strokeWidth: '67%',
              margin: 0, // margin is in pixels
              dropShadow: {
                enabled: true,
                top: -3,
                left: 0,
                blur: 4,
                opacity: 0.35
              }
            },
            dataLabels: {
              name: {
                offsetY: -10,
                show: true,
                color: '#888',
                fontSize: '14px'
              },
              value: {
                formatter: function (val) {
                  return parseInt(val);
                },
                color: '#111',
                fontSize: '30px',
                show: true,
              }
            }
          }
        },
        fill: {
          type: 'gradient',
          gradient: {
            shade: 'dark',
            type: 'horizontal',
            shadeIntensity: 0.5,
            gradientToColors: ['#62cb31'],
            //inverseColors: true,
            opacityFrom: 1,
            opacityTo: 1,
            stops: [0, 100]
          }
        },
        stroke: {
          lineCap: 'round'
        },
        labels: ['Meta del día'],
      }
    }
  },
  methods: {
    numberWithCommas: helperComma.numberWithCommas
  }
}
</script>

<style scoped>
  #chart2{
    width: 100%;
  }

  .metas {
    position: absolute;
    bottom: .5em;
    left:35%;
  }

  .meta {
    display: flex;
    justify-content: center;
  }
  .left{
    margin-right: .5em;
    color: #a3a4a6;
  }
  .right {
    font-size: .9em;
    
  }

  .height {
    height: 229px;
    max-height: 229px;
  }
</style>
