import getDataCurrency from './getDataCurrency'
import getWarehouses from './getWarehouses'
import getLastSevenDays from './getLastSevenDays'
import getSalesPerHour from './getSalesPerHour'
import getSalesPaymentMethodsOfDay from './getSalesPaymentMethodsOfDay'
import getWeeklyTrends from './getWeeklyTrends'
import getMostSoldCategories from './getMostSoldCategories'
import getMostSelledProducts from './getMostSelledProducts'
import getInfo from './getInfo'
import getAverage from './getAverage'
import getDishesSoldPerDay from './getDishesSoldPerDay'
import getDomicilies from './getDomicilies'
import saveMeta from './saveMeta'

export default {
  getDataCurrency: () => getDataCurrency(),
  getWarehouses: () => getWarehouses(),
  getLastSevenDays: (idWarehouse) => getLastSevenDays(idWarehouse),
  getSalesPerHour: (idWarehouse) => getSalesPerHour(idWarehouse),
  getSalesPaymentMethodsOfDay: (idWarehouse) => getSalesPaymentMethodsOfDay(idWarehouse),
  getWeeklyTrends: (idWarehouse) => getWeeklyTrends(idWarehouse),
  getMostSoldCategories: (idWarehouse) => getMostSoldCategories(idWarehouse),
  getMostSelledProducts: (idWarehouse) => getMostSelledProducts(idWarehouse),
  getInfo: (idWarehouse, day) => getInfo(idWarehouse, day),
  getAverage: (idWarehouse) => getAverage(idWarehouse),
  getDishesSoldPerDay: (idWarehouse) => getDishesSoldPerDay(idWarehouse),
  getDomicilies: (idWarehouse) => getDomicilies(idWarehouse),
  saveMeta: (idWarehouse, goal) => saveMeta(idWarehouse, goal),
  getAllData: (idWarehouse) => getAllData(idWarehouse)
}

const getAllData = async (id) => {
  getDataCurrency()
  getSalesPerHour(id)
  getSalesPaymentMethodsOfDay(id)
  getWeeklyTrends(id)
  getMostSoldCategories(id)
  getMostSelledProducts(id)
  getAverage(id)
  getDishesSoldPerDay(id)
  getLastSevenDays(id)
  getInfo(id)
  getDomicilies(id)
}