<template>
<div class="col-sm-6 col-xl-4">
  <div class="card mb-4">
    <h6 class="card-header with-elements">
      <div class="card-header-title">Categorias mas vendidas </div>
    </h6>
    <div class="card-body height">
      <apexchart 
        ref="pie"
        type=pie
        width='100%'
        height=200
        :options="chartOptions"
        :series="series"
      />
    </div>
  </div>
</div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'

export default {
  name: 'ChartCategorias',
  props: ['time', 'total'],
  components: {
    apexchart: VueApexCharts,
  },
  data() {
    return {
      series: this.$store.state.categorias.map((a) => a.qty),
      chartOptions: {
        labels: this.$store.state.categorias.map((a) => a.category),
        responsive: [{
          breakpoint: 480,
          options: {
            chart: {
              width: 200
            },
            legend: {
              position: 'bottom'
            }
          }
        }]
      }
    }
  },
  mounted() {
    
  }
}
</script>

<style scoped>
  #chart4{
    width: 90%;
  }

  .height {
    height: 229px;
    max-height: 229px;
  }
</style>
