import axios from 'axios'
import store from '../store'
import helperConfig from '../helpers/config'
export default async (idWarehouse) => {
  try {
    var endpoint = null;

    if (idWarehouse === null) {
      endpoint = "/dashboard/average-attention"
    } else {
      endpoint = "/dashboard/average-attention/" + idWarehouse
    }

    const res = await axios(helperConfig.getConfig('get', endpoint))
    let { data } = res
    store.state.hour = data
  } catch (error) {
    // console.error(error)
  }
}
