import axios from 'axios'
import store from '../store'
import helperConfig from '../helpers/config'
export default async (idWarehouse) => {
  try {
    var endpoint = null;

    if (idWarehouse === null) {
      endpoint = "/dashboard/most-sold-categories"
    } else {
      endpoint = "/dashboard/most-sold-categories/" + idWarehouse
    }


    const res = await axios(helperConfig.getConfig('get', endpoint))
    let { data } = res
    store.state.categorias = data
  } catch (error) {
    // console.error(error)
  }
}
