<template>
  <div class="body-dashboard">
    <div class="container-fluid">
      <div class="row space">
        <div class="col-sm-6 col-xl-4">
          <div class="flex-almacenes">
            <label class="label-almacenes">Almacenes</label>
            <div class="white ml">
              <v-select
                v-if="$store.state.almacenes.name"
                :options="[...$store.state.almacenes.name]"
                v-model="selectedLocal"
              ></v-select>
            </div>
          </div>
        </div>
      </div>

      <div class="row space">
        <Card :title="'Ventas del día'" :price="$store.state.dataToday" icon="shop"/>
        <Card :title="'Número de Facturas'" :price="$store.state.invoices_day" icon="invoice"/>
        <Card :title="'Gastos del día'" :price="$store.state.costs" icon="gastos"/>
        <Card :title="'Utilidad del día'" :price="$store.state.profit" icon="utilidad"/>
      </div>
      <div class="row mb1">
        <GraphicDay/>
        <Metodos/>
      </div>
      <div class="row" v-if="type_business === 'restaurante'">
        
        <div class="col-sm-6 col-xl-4">
          <div class="card mb-4">
            <h6 class="card-header with-elements">
              <div class="card-header-title">Domicilios</div>
            </h6>
            <div class="card-body-domicilios" >
              <ChartDomicilios v-if="$store.state.domicilios.length > 0"/>
            </div>
          </div>
        </div>
        <ChartPlatos v-if="$store.state.seriesPlatos.length > 0"/>
        <ChartPermanencia/>
      </div>
      <ChartPrincipal />
      <div class="row">
        <ChartMetas v-if="$store.state.metaPercent !== 0"/>
        <div class="col-sm-6 col-xl-4" v-else>
          <div class="card mb-4">
            <h6 class="card-header with-elements">
              <div class="card-header-title">Metas</div>
            </h6>
            <div class="card-body height flex-center">
              <!-- <a :href="getUrlEnv() + '/index.php/almacenes/editar/' + warehouse_selected.id">Configurar meta diarias aqui!</a> -->
              <a href="#" @click="show()">Configurar meta diarias aqui!</a>
            </div>
          </div>
        </div>
        <ChartCategorias v-if="$store.state.categorias.length > 0"/>
        <div class="col-sm-6 col-xl-4" v-else>
          <div class="card mb-4">
            <h6 class="card-header with-elements">
              <div class="card-header-title">Categorías mas vendidas </div>
            </h6>
            <div class="card-body" style="height: 229px;">
            </div>
          </div>
        </div>
        <ChartProductos/>
      </div>
      
      <div class="row">
        <div class="btn-group mr" role="group">
          <button
            type="button"
            class="btn btn-secondary"
            :class="{active: daySelected === 7}" @click="getDay(7)">7 días</button>
          <button
            type="button"
            class="btn btn-secondary"
            :class="{active: daySelected === 14}" @click="getDay(14)">14 días</button>
          <button
            type="button"
            class="btn btn-secondary"
            :class="{active: daySelected === 30}" @click="getDay(30)">30 días</button>
        </div>
      </div>

      <div class="row">
        <Item icon="globe" :price="$store.state.selectedDay.ventas" title="Total de ventas"/>
        <Item icon="image" :price="$store.state.selectedDay.utilidades" title="Total de utilidad"/>
        <Item icon="dollar" :price="$store.state.selectedDay.costs" title="Gastos acumulados"/>
      </div>
     
    </div>

    <!--<Preload v-if="$store.state.total.length <= 0"></Preload>-->
    <ModalMetas :idWarehouse="warehouse_selected.id"/>
  </div>

</template>

<script>
import api from '../../api/index'
import helperConfig from "../../helpers/config"
import Card from './Card'
import GraphicDay from './GraphicDay'
import Metodos from './Metodos'
import Item from './Item'
import ChartPrincipal from '../Charts/ChartPrincipal'
import ChartCategorias from '../Charts/ChartCategorias'
import ChartMetas from '../Charts/ChartMetas'
import ChartDomicilios from '../Charts/ChartDomicilios'
import ChartPlatos from '../Charts/ChartPlatos'
import ChartProductos from '../Charts/ChartProductos'
import ChartPermanencia from '../Charts/ChartPermanencia'
import ModalMetas from './Modal'
// import Preload from '../Charts/Preload'

import { mapState } from 'vuex'
export default {
  name: 'Dashboard',
  components: {
    Card,
    GraphicDay,
    ChartPrincipal,
    ChartCategorias,
    ChartMetas,
    ChartDomicilios,
    ChartPlatos,
    ChartProductos,
    ChartPermanencia,
    Item,
    Metodos,
    ModalMetas,
    // Preload
  },
  data() {
    return {
      daySelected: 7,
      selectedLocal: '',
      type_business: '',
      warehouse_selected: {},
    }
  },
  watch: {
    selectedLocal(value) {
      this.$store.state.dataToday = 0
      let found = this.$store.state.warehouses.find(a => a.name === value)
      if (found) {
        this.warehouse_selected = found
        this.getAllData(found.id)
        this.getInfo(found.id, 7)//Get info 7 dias
      }
    },
    selected(value) {
      this.selectedLocal = value
    }
  },
  methods: {
    getWarehouses: api.getWarehouses,
    getInfo: api.getInfo,
    getAllData: api.getAllData,
    getUrlEnv: helperConfig.getUrlEnv,
    getDay(day) {
      this.daySelected = day
      this.getInfo(this.warehouse_selected.id, day)
    },
    show () {
      this.$modal.show('modal')
    },
    hide () {
      this.$modal.hide('modal')
    },
  },
  async mounted() {
    let dataAuth = localStorage.getItem('api_auth')
    if (dataAuth) {
      this.type_business = JSON.parse(dataAuth).type_business
      this.$store.state.is_admin = JSON.parse(dataAuth).user.is_admin
    }
    await this.getWarehouses()
  },
  computed: mapState(['selected'])
}
</script>

<style scoped>

  .flex-center {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .height {
    height: 229px;
    max-height: 229px;
  }

  .card-body-domicilios {
    height: 219px;
    max-height: 219px;
  }
  .ml {
    margin-left: 1em;
  }

  .flex-almacenes {
    display: flex;
  }

  .white {
    background-color: #fff;
    border-radius: 5px;
  }
  .mr {
    margin-left: 1em;
  }

  .mb1 {
    margin-bottom: 1em;
  }

  .body-dashboard {
    background-color: #eee!important;
  }

  .space {
    padding-top: 1em;
  }

  .container-chart {
    background-color: #fff;
    margin: 0;
    height: 210px;
    padding: 0px 1em;
  }

  .text-right {
    text-align: right;
  }

  .title-card {
    /* background-color: rgba(0,0,0,.03); */
  }

  .label-almacenes {
    position: relative;
    top: 5px;
  }
</style>
