import axios from 'axios'
import store from '../store'
import helperData from '../helpers/data'
import helperConfig from '../helpers/config'
export default async (idWarehouse) => {
  try {
    var endpoint = null;

    if (idWarehouse === null) {
      endpoint = "/dashboard/dishes-sold-per-day"
    } else {
      endpoint = "/dashboard/dishes-sold-per-day/" + idWarehouse
    }

    const res = await axios(helperConfig.getConfig('get', endpoint))
    let { data } = res
    if (data && data !== '') {
      store.state.dishes_sold_per_day = data
      helperData.seriesPlatos(data, store)
    }    
  } catch (error) {
    // console.error(error)
  }
}
