<template>
<div id='chart2' class="col-sm-6 col-xl-4">
    <div class="card mb-4" id="chart-platos">
      <h6 class="card-header with-elements" style="margin-bottom: .5em;">
        <div class="card-header-title">Numero de platos promedio por días</div>
      </h6>
      <div class="card-body">
        <apexchart type=bar height=200 :options="$store.state.optionsPlatos" :series="$store.state.seriesPlatos"/>
      </div>
    </div>
  </div>
</template>
<script>
import VueApexCharts from 'vue-apexcharts'

export default {
  name: 'ChartPlatos',
  components: {
    apexchart: VueApexCharts,
  },
  data() {
    return {
      series: [{
        data: [1,2,3,4,5,6,7]
      }],
      chartOptions: {
        chart: {
          height: 200,
          type: 'bar',
        },
        plotOptions: {
          bar: {
            dataLabels: {
              position: 'top', // top, center, bottom
            },
          }
        },
        colors: ['#62cb31'],
        dataLabels: {
          enabled: true,
          formatter: function (val) {
            return '' + val
          },
          offsetY: -20,
          style: {
            fontSize: '12px',
            colors: ['#304758']
          }
        },
        xaxis: {
          //categories: ['Miercoles', 'Jueves', 'Viernes', 'Sabado', 'Domingo', 'Ayer', 'Hoy'],
          categories: ['Miercoles', 'Jueves', 'Viernes', 'Sabado', 'Domingo', 'Ayer', 'Hoy'],
          position: 'top',
          labels: {
            offsetY: -20,
            rotate: -45
          },
          axisBorder: {
            show: false
          },
          axisTicks: {
            show: false
          },
          crosshairs: {
            fill: {
              type: 'gradient',
              gradient: {
                colorFrom: '#62cb31',
                colorTo: '#62cb31',
                stops: [0, 100],
                opacityFrom: 0.4,
                opacityTo: 0.5,
              }
            }
          },
          tooltip: {
            enabled: true,
            offsetY: -35,
          }
        },
        fill: {
          gradient: {
            shade: 'light',
            type: 'horizontal',
            shadeIntensity: 0.25,
            gradientToColors: undefined,
            inverseColors: true,
            opacityFrom: 1,
            opacityTo: 1,
            stops: [50, 0, 100, 100]
          },
        },
        yaxis: {
          axisBorder: {
            show: false
          },
          axisTicks: {
            show: false,
          },
          labels: {
            show: false,
            formatter: function (val) {
              return '' + val
            }
          }
        }
      }
    }
  },
  methods: {
  }
}
</script>

<style scoped>
  #chart4{
    width: 90%;
  }
  .card-body {
    height: 211px;
    max-height: 211px;
  }
</style>
