import axios from 'axios'
import store from '../store'
import helperConfig from '../helpers/config'

export default async () => {
    try {
        const res = await axios(helperConfig.getConfig('get', `/dashboard/warehouses`))

        if (store.state.is_admin) {
            res.data.data.push({
                id: null,
                name: "Todos"
            })
        }

        let {data} = res.data
        let almacenes = {}
        almacenes.id = data.map(a => a.id)
        almacenes.name = data.map(a => a.name)

        store.state.almacenes = almacenes
        store.state.warehouses = data
        store.state.selected = almacenes.name[0]
        return data
    } catch (error) {
        // console.error(error)
    }
}
