import axios from 'axios'
import store from '../store'
import helperConfig from '../helpers/config'
export default async (idWarehouse, days) => {
  try {
    idWarehouse = (idWarehouse === null) ? 0 : idWarehouse;

    let filterDays = days ? `/${days}`: ''
    const url = `/dashboard/info/warehouse/${idWarehouse}${filterDays}`
    const res = await axios(helperConfig.getConfig('get', url))
    let { data } = res
    const { daily_goal, amount, profits, invoices, cost } = data
    if (!days) {
      store.state.dataToday = amount
      store.state.invoices_day = invoices
      store.state.profit = profits
      store.state.costs = cost
      if (daily_goal === 0) {
        store.state.meta = 0
        store.state.metaResto = 0
        store.state.metaPercent = 0
      } else {
        store.state.meta = daily_goal
        store.state.metaResto = daily_goal - amount
        store.state.metaPercent = (amount * 100) / daily_goal
      }
    } else {
      store.state.selectedDay.ventas = amount
      store.state.selectedDay.utilidades = profits
      store.state.selectedDay.costs = cost
    }
  } catch (error) {
    // console.error(error)
  }
}
