<template>
  <div id='chart4' >
    <apexchart 
      ref="chartDia"
      type=area
      height=200
      :options="chartOptions"
      :series="$store.state.total"
    />
  </div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'

export default {
  name: 'ChartDia',
  components: {
    apexchart: VueApexCharts,
  },
  data() {
    return {
      
      chartOptions: {
        chart: {
          zoom: {
            enabled: false
          }
        },
        labels: this.$store.state.time,
        colors: ['#62cb31'],
        dataLabels: {
          enabled: false
        }
      }
    }
  },
  mounted() {
  }
}
</script>

<style scoped>
  /*
  #chart4{
    width: 90%;
  }
   */
</style>
