<template>
  <div>
    <apexchart
      v-if="$store.state.domicilios.length > 0"
      ref="ChartDomicilios"
      type=pie
      width='100%'
      height=200
      :options="chartOptions"
      :series="series"
    />
  </div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'

export default {
  name: 'ChartDomicilios',
  components: {
    apexchart: VueApexCharts,
  },
  data() {
    return {
      series: this.$store.state.domicilios.map((a) => a.qty),
      chartOptions: {
        labels: this.$store.state.domicilios.map((a) => a.domiciliary),
        responsive: [{
          breakpoint: 480,
          options: {
            chart: {
              width: 200
            },
            legend: {
              position: 'bottom'
            }
          }
        }]
      }
    }
  }
}
</script>

<style scoped>
  .card-body-domicilios {
    height: 219px;
    max-height: 219px;
  }
</style>
