import axios from 'axios'
import store from '../store'
import helperConfig from '../helpers/config'
export default async (idWarehouse) => {
  try {
    var endpoint = null;

    if (idWarehouse === null) {
      endpoint = "/dashboard/sales-per-hour"
    } else {
      endpoint = "/dashboard/sales-per-hour/" + idWarehouse
    }

    const res = await axios(helperConfig.getConfig('get', endpoint))
    let { data } = res
    store.state.time = data.map(a => a.time)
    store.state.total = [{
      data: data.map(a => a.total)
    }]
  } catch (error) {
    // console.error(error)
  }
}
