<template>
  <div class="flex">
    <div id="chart" class="row">
      <ChartWeek :dataChartWeek="$store.state.dataChartWeek" :width="window.width"/>
      <div id="chartk" class="col-xs-12 col-md-6">
        <div class="card mb-4">
          <h6 class="card-header with-elements">
            <div class="card-header-title">Tendencias de ventas semanal</div>
          </h6>
          <apexchart type=heatmap  height=350 :options="chartOptions" :series="$store.state.dataChart"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import VueApexCharts from 'vue-apexcharts'
import ChartWeek from './ChartWeek'

export default {
  name: 'ChartPrincipal',
  components: {
    apexchart: VueApexCharts,
    ChartWeek 
  },
  props: ['allData'],
  data() {
    return {
      chartOptions: {
        dataLabels: {
          enabled: false
        },
        colors: ["#62cb31"]
      },
      dataChart: [],
      dataChartWeek: [],
      time: [],
      total: [],
      dataMeta: [],
      window: {
        width: 0
      }
    }
  },
  created() {
    window.addEventListener('resize', this.handleResize)
    this.handleResize();
  },
  destroyed() {
    window.removeEventListener('resize', this.handleResize)
  },
  methods: {
    handleResize() {
      this.window.width = window.innerWidth * .9
    }
  }
}
</script>

<style scoped>
  #chartk {
    font-family: sans-serif;
    text-align: center;
  }

  h3 {
    color: #62cb31;
  }

  @media (max-width: 600px) {
    #chart {
      display: none;
    }
  }
</style>
