import axios from 'axios'
import store from '../store'
import helperConfig from '../helpers/config'
export default async (idWarehouse) => {
  try {
    var endpoint = null;

    if (idWarehouse === null) {
      endpoint = "/dashboard/last-seven-days"
    } else {
      endpoint = "/dashboard/last-seven-days/" + idWarehouse
    }

    const res = await axios(helperConfig.getConfig('get', endpoint))
    let { data } = res
    store.state.dataChartWeek = data
    const { length } = data
    
    data.map((a, index) => {
      a.date = jsUcfirst(a.date)
      if (index === length - 1) {
        a.date = 'Hoy'
      } else if (index === length - 2) {
        a.date = 'Ayer'
      }
      store.state.data7Days.ventas += a.total
    })
  } catch (error) {
    // console.error(error)
  }
}

const jsUcfirst = (string) => {
  return string.charAt(0).toUpperCase() + string.slice(1);
}