import axios from 'axios'
import helperConfig from '../helpers/config'
export default async (idWarehouse, goal) => {
  try {
    const dataPost = {
      warehouse: idWarehouse,
      goal
    }
    const res = await axios(helperConfig.getConfig('post', `/warehouses/set-goal`, dataPost))
    let { data } = res
    return data.status
  } catch (error) {
    // console.error(error)
  }
}
