<template>
  <modal 
    name="modal"
    width="300"
  >
    <div class="header">
      <h6 class="card-header with-elements height20">
        <div class="card-header-title">Configurar Meta Diaria</div>
      </h6>
    </div>
    <div class="container height60">
      <money v-model="goal" v-bind="money"></money> 
    </div>
    <div class="buttons">
      <div class="button" @click="Guardar()">
        Guardar
      </div>
    </div>
  </modal>
</template>

<script>
import api from '../../api/index'
import helperAlert from '../../helpers/alert'
export default {
  name: 'ModalMetas',
  props: ['idWarehouse'],
  data() {
    return {
      goal: '',
      money: {
        decimal: ',',
        thousands: '.',
        prefix: '$ ',
        suffix: '',
        precision: 2,
        masked: false
      }
    }
  },
  methods: {
    saveMeta: api.saveMeta,
    setAlert: helperAlert.setAlert,
    async Guardar() {
      const statusResponse = await this.saveMeta(this.idWarehouse, this.goal)
      this.$modal.hide('modal')
      if (statusResponse) {
        this.setAlert('Meta guardada correctamente', true)
      } else {
        this.setAlert('Error con el servidor')
      }
    }
  }
}
</script>

<style scoped>
  .container {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .height20 {
    height: 20%;
  }

  .height60 {
    height: 60%;
  }

  .height80 {
    height: 80%;
  }

  .buttons {
    padding: 0 5%;
    display: flex;
    justify-content: center;
  }

  .button {
    padding: 1em;
    background-color: #28a745;
    height: 50px;
    text-align: center;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
    width: 300px;
  }
</style>
