export default {
  numberWithCommas: (x, currency) => {
    const curr = currency
    let thousands_sep = ','
    let  decimals_sep = '.'
    let decimals = 2
    if(curr) {
      thousands_sep = curr.thousands_sep ? curr.thousands_sep: ','
      decimals_sep = curr.decimals_sep ? curr.decimals_sep: ','
      decimals = curr.decimals ? parseFloat(curr.decimals): 2
    }
    let final_decimal = ''
    for (let i = 0; i < decimals; i++) { final_decimal += '0'}
    const mask = `#${thousands_sep}##0${decimals_sep}${final_decimal}`
    mask
    return parseInt(x).toString().replace(/\B(?=(\d{3})+(?!\d))/g, thousands_sep);
  },
  seriesPlatos: (series, store) => {
    store.state.seriesPlatos = [{
      data: series.map(a => a.qty)
    }]
    store.state.optionsPlatos =  {
      chart: {
        height: 200,
        type: 'bar',
      },
      plotOptions: {
        bar: {
          dataLabels: {
            position: 'top', // top, center, bottom
          },
        }
      },
      colors: ['#62cb31'],
      dataLabels: {
        enabled: true,
        formatter: function (val) {
          return '' + val
        },
        offsetY: -20,
        style: {
          fontSize: '12px',
          colors: ['#304758']
        }
      },
      xaxis: {
        categories: series.map((a, index) => {
          a.day = jsUcfirst(a.day)
          if (index === series.length-1) {
            a.day = 'Hoy'
          } else if (index === series.length-2) {
            a.day = 'Ayer'
          }
          return a.day
        }),
        position: 'top',
        labels: {
          offsetY: -20,
          rotate: -45
        },
        axisBorder: {
          show: false
        },
        axisTicks: {
          show: false
        },
        crosshairs: {
          fill: {
            type: 'gradient',
            gradient: {
              colorFrom: '#62cb31',
              colorTo: '#62cb31',
              stops: [0, 100],
              opacityFrom: 0.4,
              opacityTo: 0.5,
            }
          }
        },
        tooltip: {
          enabled: true,
          offsetY: -35,
        }
      },
      fill: {
        gradient: {
          shade: 'light',
          type: 'horizontal',
          shadeIntensity: 0.25,
          gradientToColors: undefined,
          inverseColors: true,
          opacityFrom: 1,
          opacityTo: 1,
          stops: [50, 0, 100, 100]
        },
      },
      yaxis: {
        axisBorder: {
          show: false
        },
        axisTicks: {
          show: false,
        },
        labels: {
          show: false,
          formatter: function (val) {
            return '' + val
          }
        }
      }
    }
      
  }
}

const jsUcfirst = (string) => {
  return string.charAt(0).toUpperCase() + string.slice(1);
}