import axios from 'axios'
import store from '../store'
import helperConfig from '../helpers/config'
export default async (idWarehouse) => {
  try {
    var endpoint = null;

    if (idWarehouse === null) {
      endpoint = "/dashboard/weekly-trends"
    } else {
      endpoint = "/dashboard/weekly-trends/" + idWarehouse
    }

    const res = await axios(helperConfig.getConfig('get', endpoint))
    let { data } = res
    const auxData = data.map((a) => {
      a.data.map((b) => {
        b.x = b.time
        b.y = b.value
      })
      a.name = jsUcfirst(a.day)
      return a
    })
    store.state.dataChart = auxData
  } catch (error) {
    // console.error(error)
  }
}

const jsUcfirst = (string) => {
  return string.charAt(0).toUpperCase() + string.slice(1);
}
  
