import axios from 'axios'
import store from '../store'
import helperConfig from '../helpers/config'
export default async () => {
  try {
    let { data_currency } = store.state
    const res = await axios(helperConfig.getConfig('get', '/data-currency'))
    let { data } = res
    if (data) {
      data_currency = { ...data_currency, ...data }
    }
    store.state.data_currency = data_currency
  } catch (error) {
    // console.error(error)
  }
}
